body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
#root {
    width: 100vw;
    height: 100vh;
    background-image: url(../public/image/backgroungImgCopy.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
}
.app {
    height: 100%;
    backdrop-filter: blur(25px);
    padding-top: 5vh;
}
.main {
    display: flex;
    width: 90vw;
    height: 95%;
    background-image: url(../public/image/backgroungImgCopy.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0 auto;
    border-radius: 40px;
    box-shadow: 0px 0px 20px 7px rgb(75, 75, 75);
}

.main_info {
    width: 55%;
    height: 100%;
    padding-left: 5%;
}
.main_picture {
    width: 35%;
    height: 100%;
    margin-right: 5%;
    margin-left: 5%;
    backdrop-filter: blur(20px);
    opacity: 0.9;
}
.header {
    height: 40px;
    margin: 10% 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}

.center {
    height: 35%;
    display: flex;
}
.footer {
    display: flex;
    height: 34%;
    padding-bottom: 4%;
    margin-top: 25px;
}

.header__settings {
    width: 50%;
}
.search__results {
    width: calc(75% + 40px);
    padding-top: 20px;
    grid-column: 2/3;
    justify-self: end;
    z-index: 1;
    .search__results_block {
        padding: 5px;
        margin-top: 30px;
        font-size: 18px;
        cursor: pointer;
        background-color: rgb(109, 109, 109);
        border-radius: 10px;
    }
}
.header__search {
    display: flex;
    justify-content: flex-end;
    .header__search__block {
        display: flex;
        height: 100%;
        width: calc(75% + 40px);
        input {
            height: 40px;
            width: 100%;
            font-size: 20px;
            border: none;
            border-radius: 7px 0 0 7px;
            background-color: grey;
            outline: none !important;
            &::placeholder{
                color: rgb(181, 176, 176);
            }
        }
        .background__lupa {
            display: flex;
            width: 40px;
            height: 40px;
            background-color: rgb(218, 218, 218);
            border-radius: 0 7px 7px 0;
            align-items: center;
            cursor: pointer;
        }
    }
}

.center__number {
    width: 50%;
    height: 100%;
    font-size: 160px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}
#center__weather__icon {
    width: -webkit-fill-available;
}
.center__weather {
    width: 50%;
    height: 100%;
    display: flex;
    .center__weather__icon {
        width: 50%;
        height: 100%;
    }
    .center__weather__three_days {
        padding-top: 10px;
        width: 50%;
        height: 100%;
        backdrop-filter: blur(20px);
        border-radius: 15px;
        text-align: center;
        .days {
            height: 33%;
            img {
                width: 35px;
            }
            padding-bottom: 10px;
        }
    }
}
.footer__info {
    width: 60%;
    height: 100%;
    h1 {
        font-size: 32px;
    }
    h2 {
        font-size: 14px;
    }
    span {
        font-size: 18px;
    }
    .coord {
        text-align: end;
        font-size: 14px;
    }
}
.footer__map {
    width: 40%;
    height: 100%;
    padding-left: 2%;
    iframe {
        width: 100%;
        height: 100%;
        border: none;
        border-radius: 15px;
    }
}
